import React, { useState, useEffect } from "react";
import {
  MailOutlined,
  PhoneOutlined,
  GlobalOutlined,
  HeartOutlined,
  HeartFilled,
  EditOutlined,
  DeleteFilled,
} from "@ant-design/icons";
import Popup from "./Popup";
import axios from "axios";

const Users = (props) => {
  const [heart, setHeart] = useState(false);

  const [buttonPopup, setButtonPopup] = useState(false);
  const [selectedUser, setSelectedUser] = useState({
    id: "",
    name: "",
    email: "",
    phone: "",
    website: "",
  });

  function handleButton() {
    setHeart(!heart);
  }

  function handleDelete() {
    props.onDelete(props.id);
  }

  const handleChange = (e) => {
    setSelectedUser({ ...selectedUser, [e.target.name]: e.target.value });
  };

  useEffect(() => {
    loadUser();
  }, []);

  const onSubmit = async (e) => {
    e.preventDefault();
    // props.onEdit();
    // console.log("data");

    let Response = await axios.put(
      `https://jsonplaceholder.typicode.com/users/${props.id}`,
      selectedUser
    );
    // setSelectedUser({
    //   id: "",
    //   name: "",
    //   email: "",
    //   phone: "",
    //   website: "",
    // });
    console.log("response", Response);
  };

  const loadUser = async () => {
    const result = await axios.get(
      `https://jsonplaceholder.typicode.com/users/${props.id}`
    );
    setSelectedUser(result.data);
    console.log("result", result.data);
  };

  // function handleSubmit(event) {
  //   props.onAdd(selectedUser);
  //   console.log("selected", selectedUser);

  //   event.preventDefault();
  // }

  return (
    <div className="users">
      <img
        className="bg-white"
        src={`https://avatars.dicebear.com/v2/avataaars/${props.username}.svg?options[mood][]=happy`}
      />
      <div className="userscontent">
        <h5 style={{ fontWeight: "bold" }}> {props.name}</h5>
        <p>
          <MailOutlined className="usericons" />
          Email: {props.email}
        </p>

        <p>
          <PhoneOutlined className="usericons" />
          Phone: {props.phone}
        </p>
        <p>
          <GlobalOutlined className="usericons" />
          Website: {props.website}
        </p>
      </div>

      <div className="iconcolors">
        <button className="icons" onClick={handleButton}>
          {heart ? <HeartFilled /> : <HeartOutlined />}
        </button>
        <button className="icons" onClick={() => setButtonPopup(true)}>
          <EditOutlined />
        </button>
        <button className="icons" onClick={handleDelete}>
          <DeleteFilled />
        </button>
        <Popup
          trigger={buttonPopup}
          selectedUser={selectedUser}
          setSelectedUser={setSelectedUser}
          setTrigger={setButtonPopup}
        >
          <form onClick={(e) => onSubmit(e)}>
            <h1>Basic Modal</h1>
            <h3>
              <span style={{ color: "red" }}>*</span> Name:
              <input
                type="text"
                name="name"
                value={selectedUser.name}
                required
                onChange={(e) => handleChange(e)}
              />
            </h3>
            <h3>
              <span style={{ color: "red" }}>*</span> Email:
              <input
                type="email"
                name="email"
                value={selectedUser.email}
                required
                onChange={(e) => handleChange(e)}
              />
            </h3>
            <h3>
              <span style={{ color: "red" }}>*</span> Phone:
              <input
                type="text"
                name="phone"
                value={selectedUser.phone}
                required
                onChange={(e) => handleChange(e)}
              />
            </h3>
            <h3>
              <span style={{ color: "red" }}>*</span> Website:
              <input
                type="text"
                name="website"
                value={selectedUser.website}
                required
                onChange={(e) => handleChange(e)}
              />
            </h3>

            <button onClick={() => setButtonPopup(false)}>
              <a>Ok</a>
            </button>
          </form>
        </Popup>
      </div>
    </div>
  );
};

export default Users;
